@import url(https://fonts.googleapis.com/css2?family=MonteCarlo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=MonteCarlo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  font-family: 'Raleway', sans-serif;
}

.navbar{
    background-color: rgba(0, 0, 0, 0.822);
    height: 7.5vh;
    top: 0;
    width: 100vw;
    position: -webkit-sticky;
    position: sticky;

} 

#logo{ 
 margin-top: 1.3vh;
 margin-left: 14%;
 font-size: calc(18px + 3vh);
 color: rgba(255, 255, 255, 0.7);
}

#logo:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  transition: 0.5s ease-out;
 }

 #logo:active{
  color: royalblue;
 }
 
#menuToggle{
  margin-left: 5%;
  opacity: 0.1;
  justify-content: center;
  align-self: center;
  
}

#menuToggle:hover{
  opacity: 1;
  transition: 0.5s ease-in-out;
  
}

.navbarItem{
    color: rgba(255, 255, 255, 0.6);
    text-decoration: none;
    margin-right: 80px;
    font-size: 15px;
    font-weight: 600;
}
.navbarItem:hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  transition: 0.5s ease-in;
}
.navbarItem:active{
    color: royalblue;
}

.navbarItems{
  display: flex;
  margin-top: -37px;
  margin-left: 24%;
}


@media screen and (max-width: 999px){
  .navbarItems{
    display: none;
  }
  #logo{
    display: none;
  }
}

@media screen and (min-width: 1000px) {
  #menuToggle{
      display: none;
  }
}
.toggleButton{
    justify-self: center;
    flex-direction: column;
    /* height: 24px; */
    background: transparent;
    cursor: pointer;
    padding: 0;
    margin-left: 10px;
}

.openButton{
    padding-top: 0.5vh;
    color: rgba(255, 255, 255, 0.5);
}


@media screen and (min-width: 1000px) {
    .menu{
        display: none;
    }
}
.homepage{
    /* margin-top: 20px; */
    background-repeat: no-repeat;
    background: url(/emerald-laketown/static/media/dawn.ec98bc7f.jpg);
    background-size: cover;
    background-position: center;
    height: 100vh;
    overflow-x: hidden;
    width: 100vw;
}

#heading{
    font-size: calc(35px + 2.1vw);
    text-align: center;
    font-weight: 700;
    text-shadow: 3px 3px rgba(29, 29, 27, 0.2);
    text-transform: capitalize;
    color: rgb(1, 85, 163);
    font-family: 'MonteCarlo', cursive;
    margin: auto;
}

.eltParagraph{
    margin: auto;
    text-align: justify;
    font-size: calc(21px + 1.5vw);
    color: black;
    width: 90vw;
}

.image{
        margin-top: -2px;
        width: 100%;
        height: 100%;  
}
img{
    border: 2px outset whitesmoke;
}

.enter{
    text-align: center;
    margin: auto;
    margin-top: 2%;
} 

.enterLink{
    border: none;
    outline: none;
}

@media screen and (min-width: 1099px){
    #heading{
        font-size: 4.5rem;
        margin: -20px auto -20px auto;
    }
}
*{
    font-family: 'Gowun Dodum', sans-serif;
    font-size: 20px;
}

body{
    background-color: whitesmoke;    
}

.paper{
    height: 50px;
    width: 100vw;
}

.eltHomes{
    margin-left: 18%; 
    padding-top: 14px; 
    font-size: 18px;
   font-weight: 500;
   align-items: center;
   color: black;
   text-decoration: none;  
}

.image1{
    width: 100%;
    height: 91.2%;
    border: none;
    margin-top: -17px;
}
.houseType{
    width: 100vw;
    overflow: hidden;
    pointer-events: none;
    overflow-x: hidden;
}
.imgDiv{
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
    margin-top: -19px;
}

.photoTitle{
    text-align: center;
    opacity: 0.7;
    font-size: 20px;
    margin-top: 32px;
}


.homeLinkDiv{
    margin-top: 3%;
    padding-bottom: 2%;  
    text-align: center;
    pointer-events: all;
 } 
 .homeLink:hover{
    font-weight: 600;
 }


.facPage{
   margin-bottom: 0;
   width: 100%;
   overflow-x: hidden;
} 

.facText{
   margin-left: 8px; 
   font-size: calc(18px + 2vw);
   font-weight: 600;
   color: black;
   text-align: center;
   font-family: 'MonteCarlo', cursive;   
   width: 100vw;
   margin-top: -1%;

}
.facilities{
   overflow-y: hidden;
   padding-bottom: 0;
}
.line1{
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
   justify-content: center;
   width: 100vw;
   align-items: center;
}

.videoBox{
   margin: 5px;
}

.video{
   width: 460px;
   pointer-events: none;
   border-radius: 5px;
   margin: auto;
}

.caption{
   color: rgba(0, 0, 0, 0.5);
   text-align: center;
   font-family: 'Raleway', sans-serif;
   font-style: italic;
   font-size: calc(16px);
   margin-top: -2px;
}

.backDiv{
   margin-top: 0.5%;
   margin-bottom: 3%;
   text-align: center;

}
.back{
    width: 100px;
}

.back:hover{
    font-weight: 600;
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
   transition: 1s ease-in-out;
}


.sideDrawer{
    height: 100%;
    background: linear-gradient(rgb(102, 167, 219, 0.9), rgba(255, 255, 255, 0.9));
    padding-left: 8px;
    box-shadow: 1px 0px 5px rgba(106, 194, 245, 0.3);
    position: fixed;
    top: 0;
    left: 0; 
    width: 50%;
    max-width: 250px;

}

#menu{ 
    color: rgba(255, 255, 255, 0.8);
    font-size: 2.3rem;
    margin-top: 5px;
    margin-left: 83%;
    cursor: pointer;
}

.sideLinks{
    padding: 25%;
    padding-top: 50%;
      font-family: 'Gowun Dodum', sans-serif;
      
}

.sideHome{
    margin-left: 30px;
    margin-bottom: 20%;
    margin-top: 40%;  
    color: rgba(0, 0, 0, 1);
}

.sideHome:hover{
  color: royalblue; 
  transition: 0.5s ease-in-out;
}

.sideHome:active{
    color: rgb(255, 255, 255); 
  }

.side{
    display: block;
    padding: 5%;
    font-weight: 600;
    text-decoration: none;
    color: rgba(0, 0, 0, 1);
}

.side:hover{
    color: royalblue;
    cursor: pointer;
    transition: 0.5s ease-in-out;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    font-weight: 500;
}

.side:active{
    color: rgb(255, 255, 255);
    cursor: pointer;

}


@media screen and (min-width: 1000px){
    #menuDiv{
        display: none;
    }
.sideDrawer{
    display: none;
}
}
